@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
}
.app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    color: #222;
}

header {
    flex-basis: 40vh;
}

main {
    display: flex;
    flex-grow: 1;
    padding: 10px;
}

aside {
    flex-basis: 280px;
}

.page {
    flex-grow: 1;
}

footer {
    text-transform: uppercase;
    padding: 10px 0;
    background-color: #000;
    color: white;
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-height: 100%;
    min-width: 100%;
}
nav.main {
    padding: 5px 30px 0 5px;
    font-size: 20px;
}

ul {
    list-style: none;
}


nav.main a {
    text-decoration: none;
    display: block;
    text-transform: uppercase;
    margin: 15px 0;
    padding: 8px;
    transition: .2s;
    font-weight: bold;
}

nav.main a:hover {
    transform: translateX(20px);
}

nav.main a.active {
    transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
    border-left: 10px solid green;
}

nav.main li:nth-child(2) a.active {
    border-left: 10px solid orange;
}

nav.main li:nth-child(3) a.active {
    border-left: 10px solid blue;
}

nav.main li:nth-child(4) a.active {
    border-left: 10px solid red;
}
.contact h1 {
    text-align: center;
}


.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    display: block;
    margin: 20px auto;
    line-height: 1.6;
}

.contact button {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 80%;
    margin-top: 20px;
    border: 2px solid black;
    border-radius: 10px;
    padding: 10px;
    font-size: 24px;
    text-transform: uppercase;
    font-family: 'Raleway';
    font-weight: bold;
    cursor: pointer;
}
.products h2 {
    margin-bottom: 30px;
}

.products ul {
    list-style: none;
}

.products a {
    text-decoration: none;
    text-transform: uppercase;
    display: block;
    padding: 10px;
    margin: 20px;
}
